<template>
  <div>
    <mu-appbar style="width: 100%;" :color="$store.state.color" class="header">
      <a @click="()=>{
          $router.go(-1)
          }">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span>数据详情</span>
      <!-- <a >
        <mu-button icon slot="right" class="icon" size="20">
          <mu-icon value="add"></mu-icon>
        </mu-button>
      </a>-->
    </mu-appbar>
    <div class="ContainerContent" v-for="(item,index) in itemListContent" :key="index">
      <div class="backContent">
        <div class="content">
          <div class="flexRow">
            <div>
              <span>污染编号</span>
            </div>
            <div>{{item.name}}</div>
          </div>
        </div>
        <div class="content">
          <div class="flexRow">
            <div>
              <span>上报时间</span>
            </div>
            <div>{{item.gmtCreate}}</div>
          </div>
        </div>
        <div class="content">
          <div class="flexRow">
            <div>
              <span>水站名称</span>
            </div>
            <div>{{item.station}}</div>
          </div>
        </div>
        <div class="content">
          <div class="flexRow">
            <div>
              <span>超标因子</span>
            </div>
            <div>{{item.factor}}</div>
          </div>
        </div>
        <div class="content">
          <div class="flexRow">
            <div>
              <span>超标企业</span>
            </div>
            <div>{{item.enterprise}}</div>
          </div>
        </div>
        <div class="content">
          <div class="flexRow">
            <div>
              <span>上报人</span>
            </div>
            <div>{{item.person}}</div>
          </div>
        </div>
      </div>
      <div class="backContent">
        <div class="content">
          <div>
            <span>超标原因</span>
          </div>
          <div>
            <textarea name class="textArea" disabled :value="item.reason"></textarea>
          </div>
        </div>
        <div class="content">
          <div>
            <span>备注</span>
          </div>
          <div>
            <textarea name class="textAreaTwo" disabled :value="item.remark"></textarea>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Vue from "vue";
import { ImagePreview } from "vant";
Vue.use(ImagePreview);
export default {
  data() {
    return {
      itemListContent: []
    };
  },
  created() {
    var arr = [];
    console.log(this.$store.state.pollutionData);
    arr.push(this.$store.state.pollutionData);
    this.itemListContent = arr;
  },
  methods: {
    
  }
};
</script>
<style scoped>
.flexRow {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40px;
  border-bottom: 1px solid rgb(231, 231, 231);
}
.content {
  /* background-color: white; */
  padding: 10px 20px;
}
.ContainerContent {
  padding: 10px;
}
.backContent {
  background-color: white;
  border-radius: 10px;
  margin-bottom: 10px;
}
.textArea {
  width: 100%;
  height: 100px;
  border-radius: 10px;
  border: 1px rgb(206, 204, 204) solid;
  background-color: white;
  padding: 10px;
}
.textAreaTwo {
  width: 100%;
  height: 50px;
  border-radius: 10px;
  border: 1px rgb(206, 204, 204) solid;
  background-color: white;
  padding: 10px;
}
</style>


